import React, { createContext, Component } from "react";

//Create a context.
export const UserTransactionDataContext = createContext();

class UserTransactions {
	constructor(name) {
		this.id = (Math.random() * 10000).toFixed(5).replace(".", "");
		this.name = name;
	}
}
class UserTransaction__Main extends UserTransactions {
	constructor(name) {
		super(name);
		(this.allTransactionsDate = []),
			(this.transactionHistory = []),
			(this.isDeleted = false);
		this.notifyUser = false;
		this.notifyUserCount = 3;
		this.isSettled = false;
	}
}
class UserTransaction_Main_HistoryThread extends UserTransactions {
	constructor(name, amount, date, description) {
		super(name);
		this.amount = amount;
		this.transactionDate = date;
		this.description = description;
		this.isImportant = false;
	}
}
class UserTransactionDataContextProvider extends Component {
	constructor(props) {
		super(props);
		this.state = {
			name: "",
			description: "",
			//dateofUserAdded: "",
			dateOfTransaction: "",
			amount: 0,
			userTransactionsInfo: [],
		};
		(this.UserAddHistory = ""),
			(this.doesInputErrorExist = false),
			(this.addDataToUserInfo = this.addDataToUserInfo.bind(this));
	}

	addDataToUserInfo(formBodyName) {
		if (formBodyName == "addUser") {
			if (this.state.name !== "") {
				this.setState({
					userTransactionsInfo: [
						...this.state.userTransactionsInfo,
						new UserTransaction__Main(this.state.name),
					],
				});
			} else {
				alert("Likhde input main kuch.!!!");
			}
		} else {
			if (this.state.name !== "") {
				const userData = this.state.userTransactionsInfo;
				const [activeTransactionHistoryUser] = userData.filter(
					(item) => item.name == this.UserAddHistory
				);
				activeTransactionHistoryUser.transactionHistory.push(
					new UserTransaction_Main_HistoryThread(
						this.UserAddHistory,
						this.state.amount,
						this.state.dateOfTransaction,
						this.state.description
					)
				);
				console.log(activeTransactionHistoryUser);
			} else {
				alert("User toh add karo pehle.!!!");
			}
		}
	}
	setName = (e) => {
		this.setState({
			name: e.target.value,
		});
		this.validateUserFormInput(e.target);
	};

	setDateOfTransaction = (e) => {
		this.setState({
			dateOfTransaction: e.target.value,
		});
		this.validateUserFormInput(e.target);
	};
	setAmount = (e) => {
		this.setState({
			amount: e.target.value,
		});
		this.validateUserFormInput(e.target);
	};
	setDescription = (e) => {
		this.setState({
			description: e.target.value,
		});
		this.validateUserFormInput(e.target);
	};
	changeUserAddHistory = (activeUser) => {
		this.UserAddHistory = activeUser;
	};
	validateUserFormInput = (targetInput) => {
		if (targetInput.value == "" || !/[a-zA-Z0-9]+/.test(targetInput.value)) {
			targetInput.classList.add("error-input");
			if (targetInput.nextElementSibling.classList.contains("d-none")) {
				targetInput.nextElementSibling.classList.remove("d-none");
				targetInput.nextElementSibling.classList.add("d-block");
			}
			this.doesInputErrorExist = true;
		} else {
			targetInput.classList.remove("error-input");
			if (targetInput.nextElementSibling.classList.contains("d-block")) {
				targetInput.nextElementSibling.classList.remove("d-block");
				targetInput.nextElementSibling.classList.add("d-none");
			}
			this.doesInputErrorExist = false;
		}
	};
	render() {
		return (
			<UserTransactionDataContext.Provider
				value={{
					...this.state,
					addDataToUserInfo: this.addDataToUserInfo,
					setName: this.setName,
					setDateOfTransaction: this.setDateOfTransaction,

					setAmount: this.setAmount,
					setDescription: this.setDescription,
					changeUserAddHistory: this.changeUserAddHistory,
				}}
			>
				{this.props.children}
			</UserTransactionDataContext.Provider>
		);
	}
}

export default UserTransactionDataContextProvider;



