import React, { Component } from "react";
import "./MoneyGiven.css";
import HeadSubHead from "../RoutePagesHead_SubHead/HeadSubHead";
import RouteSearchSort from "../RoutePagesSearch_Sort/RouteSearchSort";
import AddTransactionModal from "../../AddTransactionModal/AddTransactionModal";
import UserAddIcon from "../../../images/user-plus.svg";
//import noTransactionEntryImage from "../../NoTransactionEntry";
import UserTransactionThumbnailsContainer from "../../UserTransactionThumbnailsContainer/UserTransactionThumbnailsContainer";
import { UserTransactionDataContext } from "../../../Contexts/UserTransactionDataContext";

class MoneyGiven extends Component {
	static contextType = UserTransactionDataContext;

	constructor(props) {
		super(props);
		this.state = {
			isModalContainerVisible: false,
		};
		this.toggleModalContainerVisibility = this.toggleModalContainerVisibility.bind(
			this
		);
	}
	toggleModalContainerVisibility(isVisible) {
		this.setState({
			isModalContainerVisible: isVisible,
		});
	}
	componentDidMount() {
		document.title = "Money Given - TODO ";
	}
	render() {
		return (
			<div className="money-given-container">
				<div className="container">
					<HeadSubHead
						head="You Have Given Money to These People"
						subhead="Do not forget to take the money back."
					/>
					<RouteSearchSort />
					<UserTransactionThumbnailsContainer
						doesUserExist="false"
						theme={this.props.theme}
						selectedTheme={this.props.selectedTheme}
					/>
					<button
						onClick={() => this.toggleModalContainerVisibility(true)}
						className="d-flex justify-content-center align-items-center text-white open-add-transaction-modal-btn"
					>
						<img
							src={UserAddIcon}
							alt="Add User Icon"
							className="img-fluid add-user-icon ml-2"
						/>
					</button>
				</div>
				<AddTransactionModal
					isModalContainerVisible={this.state.isModalContainerVisible}
					toggleModalContainerVisibility={this.toggleModalContainerVisibility}
				/>
			</div>
		);
	}
}

export default MoneyGiven;



