import React, { Component } from "react";
import "./App.css";
import { BrowserRouter as Router, Route } from "react-router-dom";

/*
 Custom Modules exported here
 */
import Navbar from "./Components/Navbar";
import TransactionHomepageEntities from "./Components/TransactionHomepageEntities";
import TodoHome from "./Components/TodoHome";
import MoneyGiven from "./Components/InternalRoutes/MoneyGivenRoute/MoneyGiven";
import MoneyToTake from "./Components/InternalRoutes/MoneyToTakeRoute/MoneyToTake";
import ImportantTransaction from "./Components/InternalRoutes/ImportantTransactionsRoute/ImportantTransaction";
import SettledTransaction from "./Components/InternalRoutes/SettledTransactionsRoute/SettledTransaction";
import ComponentTester from "./Components/ComponentTester";
import LandingPage from "./Components/LandingPage/LandingPage";
import Login from "./Components/LoginRegister/Login";
import Register from "./Components/LoginRegister/Register";
import UserTransactionDataContextProvider from "./Contexts/UserTransactionDataContext";
import UserAuthProvider from "./Contexts/UserAuthContext";
import ErrorSuccessInfoProvider from "./Contexts/ErrorSuccessInfoModalContext";

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			userTransactionsInfo: [],
			showNavbarMoreOptions: false,
			theme: {
				dark: {
					//navColor: " #1a1a1a",
					color: "#1a1a1a",
					boxShadow: "5px 5px 25px 0 rgba(255,255,255,0.2)",
					addUserBtnColor: "#fff",
				},
				light: {
					//navColor: "#fff",
					color: "#fff",
					boxShadow: "5px 5px 25px 0 rgba(46,61,73,.2)",
					backgroundSize: "cover",
					addUserBtnColor: "var(--green)",
				},
			},
			selectedTheme: "light",
		};

		this.toggleAppThemeChange = this.toggleAppThemeChange.bind(this);
		this.toggleShowNavbarMoreOptions = this.toggleShowNavbarMoreOptions.bind(
			this
		);
	}

	toggleAppThemeChange(themeValue) {
		this.setState({
			selectedTheme: themeValue ? "dark" : "light",
		});
		if (themeValue) {
			document.body.className = "dark-theme-body";
		} else {
			document.body.className = "light-theme-body";
		}
	}
	componentDidMount() {
		document.body.className = "light-theme-body";
	}
	toggleShowNavbarMoreOptions(showNavbar) {
		this.setState({
			showNavbarMoreOptions: showNavbar,
		});
	}

	render() {
		return (
			<UserAuthProvider>
				<UserTransactionDataContextProvider>
					<Router>
						<div>
							<Navbar
								toggleAppThemeChange={this.toggleAppThemeChange}
								selectedTheme={this.state.selectedTheme}
								theme={this.state.theme[this.state.selectedTheme]}
								toggleShowNavbarMoreOptions={this.toggleShowNavbarMoreOptions}
								showNavbarMoreOptions={this.state.showNavbarMoreOptions}
							/>

							<Route exact path="/" component={() => <LandingPage />} />
							<ErrorSuccessInfoProvider>
								<Route
									path="/todo-money"
									component={() => <LandingPage theme={this.state.theme} />}
								/>
								<Route path="/user-auth/login" component={() => <Login />} />
								<Route
									path="/user-auth/register"
									component={() => <Register />}
								/>
								<Route
									path="/todo-home"
									component={() => <TodoHome theme={this.state.theme} />}
								/>
								<Route
									path="/money-given"
									component={() => (
										<MoneyGiven
											theme={this.state.theme}
											selectedTheme={this.state.selectedTheme}
										/>
									)}
								/>
								<Route path="/money-to-take" component={MoneyToTake} />
								<Route
									path="/important-transactions"
									component={ImportantTransaction}
								/>
								<Route
									path="/settled-transactions"
									component={SettledTransaction}
								/>
								<Route
									path="/component-test"
									component={() => (
										<ComponentTester
											theme={this.state.theme}
											selectedTheme={this.state.selectedTheme}
										/>
									)}
								/>
							</ErrorSuccessInfoProvider>
						</div>
					</Router>
				</UserTransactionDataContextProvider>
			</UserAuthProvider>
		);
	}
}

export default App;



