import React from "react";
import { NavLink } from "react-router-dom";

function TransactionHomepageEntities() {
	return (
		<section className="transaction-entities-container">
			<div className="container">
				<div className="row mb-5 align-items-center d-flex justify-content-center">
					<NavLink
						to="/money-given"
						className="transaction-entity__money-given d-flex justify-content-center align-items-center transaction-entity col-xl-4 col-lg-4 col-md-4 col-sm-4 col-5 mr-xl-5 mr-lg-5 mr-md-5 mr-sm-5 mr-2"
					>
						Money Given
					</NavLink>
					<NavLink
						to="/money-to-take"
						className="transaction-entity__money-to-take d-flex justify-content-center align-items-center transaction-entity col-xl-4 col-lg-4 col-md-4 col-sm-4 col-5"
					>
						Money to be Taken
					</NavLink>
				</div>
				<div className="row align-items-center d-flex justify-content-center">
					<NavLink
						to="/important-transactions"
						className="transaction-entity__imp-transact d-flex justify-content-center align-items-center transaction-entity col-xl-4 col-lg-4 col-md-4 col-sm-4 col-5 mr-xl-5 mr-lg-5 mr-md-5 mr-sm-5 mr-2"
					>
						Important Transactions
					</NavLink>
					<NavLink
						to="/settled-transactions"
						className="transaction-entity__settled-transact d-flex justify-content-center align-items-center transaction-entity col-xl-4 col-lg-4 col-md-4 col-sm-4 col-5"
					>
						Settled Transactions
					</NavLink>
				</div>
			</div>
		</section>
	);
}
export default TransactionHomepageEntities;



