import React from "react";
import "../../Components/UserTransactionDataThumbnail.css";
import clockIcon from "../../images/clock.png";
import userImage from "../../images/user_image.png";

function UserTransactionDataThumbnail_History_Thread(props) {
	const { theme, selectedTheme } = props;
	const addTagLineSpecificToURL = {
		"/component-test": {
			tagline: "Vaibhav has given RS XXX to USER",
			amountSummary:
				"This is the total amount of money Vaibhav has given to USER ",
		},
		"/money-to-take": {
			tagline: "Vaibhav has to take RS XXX from USER",
			amountSummary:
				"This is the total amount of money Vaibhav has to take from USER ",
		},
	};
	console.log();
	return (
		<div
			className="user-transaction-thumbnail-history-container px-2  py-4"
			style={{
				background:
					selectedTheme == "light" ? theme.light.color : theme.dark.color,
				boxShadow:
					selectedTheme == "light"
						? theme.light.boxShadow
						: theme.dark.boxShadow,
			}}
		>
			<div className="row d-flex justify-content-center m-0  ">
				<div className="col-xl-2 col-lg-2 col-md-2 p-0 ml-xl-0 ml-lg-0 ml-md-0 ml-2 d-flex justify-content-xl-center justify-content-lg-center mb-xl-0 mb-lg-0 mb-2 justify-content-start">
					<img
						src={userImage}
						className="img-fluid  user-transaction-data-thumnail__image"
					/>
				</div>
				<div
					style={{
						color:
							selectedTheme == "light" ? theme.dark.color : theme.light.color,
					}}
					className="col-xl-6 col-lg-6 col-md-6 d-flex flex-column align-items-start justify-content-between"
				>
					<h1 className="user-transaction-data-thumnail__name  mb-xl-0 mb-lg-0 mb-3">
						USER
					</h1>
					<p className="user-transaction-data-thumnail__amount-summary mb-2 text-left">
						This transaction was made on :
					</p>
					<div className="user-transaction-data-thumnail__date mt-0 px-2  py-1 d-flex justify-content-center align-items-center">
						<img
							src={clockIcon}
							className="img-fluid mr-2 user-transaction-data-thumnail__date-clock-icon"
							alt="Clock icon"
						/>
						<h6 className="user-transaction-data-thumnail__date-text text-white  m-0">
							23-04-2020
						</h6>
					</div>

					<div className="user-transaction-data-thumbnail__tagline-row d-flex align-items-center">
						<span className="pl-0 d-flex justify-content-center align-items-center">
							&#9888;
						</span>
						<p
							style={{
								color:
									selectedTheme == "light"
										? theme.dark.color
										: theme.light.color,
							}}
							className="  user-transaction-data-thumnail__tagline  my-3"
						>
							{addTagLineSpecificToURL[window.location.pathname]["tagline"]}
						</p>
					</div>
					<p
						style={{
							color:
								selectedTheme == "light" ? theme.dark.color : theme.light.color,
						}}
						className="  user-transaction-data-thumnail__description  my-3"
					>
						This Denmark Eats Engineering team is responsible for building
						product catalog and inventory system that powers the Uber Eats
						experience and related verticals, such as groceries.
					</p>
					{/*
					<p
						className="user-transaction-data-thumnail__description"
						style={{
							color:
								selectedTheme == "light" ? theme.dark.color : theme.light.color,
						}}
					>
						I'm a little confused why we keep making class components...
					</p>
					*/}
				</div>
				<div
					className="col-xl-4 col-lg-4 col-md-4 amount-payable__container d-flex flex-column align-items-xl-center align-items-lg-center align-items-md-center align-items-end"
					style={{
						color:
							selectedTheme == "light" ? theme.dark.color : theme.light.color,
					}}
				>
					<h1 className=" user-transaction-data-thumnail__amount font-weight-bold text-xl-center text-lg-center text-md-center text-right mt-xl-5 mt-lg-5 mt-2">
						Rs. XXX
					</h1>
				</div>
			</div>
			<div className="user-transaction-data-thumnail__action-btns mt-1 w-100">
				<div className="action-btns__CRUD d-flex w-100 justify-content-end w-100">
					<button className="action-btn__important  text-white px-2 mr-xl-5 mr-lg-5 mr-md-5 mr-sm-5 mr-0 transaction-thumbnail-action-btn">
						Mark Important
					</button>
				</div>
			</div>
		</div>
	);
}

export default UserTransactionDataThumbnail_History_Thread;



