import React, { Component, createContext } from "react";

export const UserAuth = createContext();

class UserAuthProvider extends Component {
	constructor(props) {
		super(props);
		this.state = {
			name: "",
			password: "",
			isUserLoggedIn: false,
		};
	}
	storeUserInfo = () => {
		//Create a user in LocalStorage
		localStorage.setItem("User", JSON.stringify(this.state));
	};
	setUserAuthName = (e) => {
		this.setState({
			name: e.target.value,
		});
	};
	setUserAuthPassword = (e) => {
		this.setState({
			password: e.target.value,
		});
	};
	changeUserAuthLoginStatus = (loginStatus) => {
		this.setState({
			isUserLoggedIn: loginStatus,
		});
		this.storeUserInfo();
	};
	render() {
		return (
			<UserAuth.Provider
				value={{
					...this.state,
					storeUserInfo: this.storeUserInfo,
					setUserAuthName: this.setUserAuthName,
					setUserAuthPassword: this.setUserAuthPassword,
					changeUserAuthLoginStatus: this.changeUserAuthLoginStatus,
				}}
			>
				{this.props.children}
			</UserAuth.Provider>
		);
	}
}

export default UserAuthProvider;



