import React, { Component } from "react";
import "./MoneyToTake.css";
import HeadSubHead from "../RoutePagesHead_SubHead/HeadSubHead";
import RouteSearchSort from "../RoutePagesSearch_Sort/RouteSearchSort";

class MoneyToTake extends Component {
	constructor(props) {
		super(props);
		this.state = {};
	}
	componentDidMount() {
		document.title = "Money to be Taken - TODO ";
	}
	render() {
		return (
			<div className="money-to-take-container">
				<div className="container">
					<HeadSubHead
						head="You Have to Take Money From These People"
						subhead="Click on the user to load full history"
					/>
					<RouteSearchSort />
				</div>
			</div>
		);
	}
}

export default MoneyToTake;



