import React, { useState } from "react";
import feedbackImage from "../../images/feedback.svg";
import "./FeedbackForm.css";
import { db } from "../Firebase";

function FeedbackForm(props) {
	const { theme, selectedTheme } = props;
	const [name, setName] = useState("");
	const [email, setEmail] = useState("");
	const [feedback, setFeedback] = useState("");
	const [disabledSubmit, setDisabledSubmit] = useState(true);
	const classBasedOnTheme = {
		light: "light-theme-input text-dark",
		dark: "dark-theme-input text-white",
	};
	const handleSubmit = (e) => {
		e.preventDefault();
		db.collection("User-Feedbacks")
			.add({
				name: name,
				email: email,
				feedback: feedback,
			})
			.then((res) => console.log(res))
			.catch((err) => console.log(err));
	};

	return (
		<footer
			className="feedback-form-footer py-2"
			style={{
				background:
					selectedTheme == "light" ? theme.light.color : theme.dark.color,
			}}
		>
			<div className="container">
				<h1
					className="no-transaction-entry-text text-center mb-3"
					style={{
						color:
							selectedTheme == "light" ? theme.dark.color : theme.light.color,
					}}
				>
					Feedback Form
				</h1>
				<div className="row w-100 h-100 m-0">
					<div className="col-xl-6 col-lg-6 col-md-6 col-12 d-flex justify-content-center align-items-center">
						<img
							src={feedbackImage}
							alt="Feedback Icon"
							className="feedback-icon "
						/>
					</div>
					<div className="col-xl-6 col-lg-6 col-md-6 col-12 pl-xl-5 pl-lg-5 pl-md-5 pl-2 d-flex flex-column justify-content-center align-items-center">
						<form className="feedback-form w-100">
							<h2
								className="feedback-form-label name-label"
								style={{
									color:
										selectedTheme == "light" ? "var(--blue)" : "var(--green)",
								}}
							>
								Name:
							</h2>
							<input
								type="search"
								name="name"
								onChange={(e) => setName(e.target.value)}
								placeholder="Enter your name"
								className={"mt-1 p-2 " + classBasedOnTheme[selectedTheme]}
							/>
							<h2
								className="feedback-form-label mt-3 email-label"
								style={{
									color:
										selectedTheme == "light" ? "var(--blue)" : "var(--green)",
								}}
							>
								Email:
							</h2>
							<input
								type="email"
								name="email"
								onChange={(e) => setEmail(e.target.value)}
								placeholder="Enter your email"
								className={"mt-1 p-2 " + classBasedOnTheme[selectedTheme]}
							/>
							<h2
								className="feedback-form-label mt-3 feedback-label"
								style={{
									color:
										selectedTheme == "light" ? "var(--blue)" : "var(--green)",
								}}
							>
								Feedback:
							</h2>
							<textarea
								type="search"
								name="feedback"
								onChange={(e) => setFeedback(e.target.value)}
								placeholder="Provide your valuable feedback here"
								className={
									"mt-1 mb-2 p-2 feedback-input " +
									classBasedOnTheme[selectedTheme]
								}
							></textarea>
							<input
								type="submit"
								value="Submit"
								onClick={(e) => handleSubmit(e)}
								className="submit-feedback-form-btn mt-3 text-white"
								style={{
									background:
										selectedTheme == "light" ? "var(--blue)" : "var(--green)",
								}}
							/>
						</form>
					</div>
				</div>
			</div>
		</footer>
	);
}

export default FeedbackForm;



