import React, { useState, useContext } from "react";
import { UserAuth } from "../../Contexts/UserAuthContext";
import "./LoginRegister.css";

function LoginRegisterFormBody() {
	const { setUserAuthName, setUserAuthPassword } = useContext(UserAuth);
	return (
		<div className="login-register-form-body-container my-3">
			<div className="user-auth-form-body">
				<h2>Name:</h2>
				<input
					type="search"
					placeholder="Enter name"
					name="uname"
					onChange={(e) => setUserAuthName(e)}
					className="mb-3 p-2"
				/>
				<h2>Password:</h2>
				<input
					type="password"
					placeholder="Enter password"
					name="upass"
					onChange={(e) => setUserAuthPassword(e)}
					className=" p-2"
				/>
			</div>
		</div>
	);
}
export default LoginRegisterFormBody;



